<template>
    <div>
        <div v-if="rooms" class="home">
            <div class="nav">
                <router-link
                    :to="`/room/${room.id}`"
                    v-for="(room,idx) in rooms"
                    :key="room.id"
                    @mouseenter.native="currentRoomNo=idx"
                >{{room.name}}</router-link>
            </div>
            <div class="roomInfo">
                <p class="roomInfo-no">0{{currentRoomNo+1}}</p>
                <p class="roomInfo-name">{{currentRoomName}}</p>
            </div>
        </div>
        <Spin v-else fix>
            <Icon type="ios-loading" size="18" class="spin-icon-load"></Icon>
            <div>Loading</div>
        </Spin>
    </div>
</template>

<script>
import axios from 'axios';
export default {
    name: 'home',
    data() {
        return {
            rooms: null,
            currentRoomNo: 0,
        }
    },
    computed: {
        currentRoomName() {
            return this.rooms[this.currentRoomNo] && this.rooms[this.currentRoomNo].name;
        }
    },
    methods: {
        getRooms() {
            axios.get('https://challenge.thef2e.com/api/thef2e2019/stage6/rooms', {
                headers: {
                    'Authorization': 'Bearer 4jGwjfznoX5ftKlftGTPRrm8DSTpZAzILdyoxCvtTZGQBSbSJzsEN0ldgOg7',
                    'accept': 'application/json'
                },
            })
                .then(res => {
                    if (res.data.success) {
                        let preloadImg = new Image();
                        preloadImg.onload = () => this.rooms = res.data.items;
                        preloadImg.src = 'https://source.unsplash.com/1200x1080?wallpapers';
                    }
                })
                .catch(err => {
                    console.log(err);
                });
        },
    },
    created() {
        this.getRooms();
    },
}
</script>

